define([
    'jquery',
], function($) {

    "use strict";

    var handler = {
        refreshurl: '',
        catchpanelrefresh: function () {
            window.location.replace(handler.refreshurl);
        }
    };

    return {
        init: function (refreshurl) {
            handler.refreshurl = refreshurl;
            $(document).on('reset done-saving-show-next', handler.catchpanelrefresh);
        }
    };
});
